#editor{
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 100px;
    margin-top: 50px;
    margin-left: 40px;
    overflow-y: scroll;
}
label{
    position: absolute;
    left:300px;
    top:29px;
    color: #7c6172;
  font-size: 16px;
  font-family: Arial, sans-serif;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  text-decoration:wavy;
  text-transform: uppercase;
}

textarea{
    background-color: #c3aeb5;
}

#preview{
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 100px;
    margin-top: 50px;
    margin-left: 40px;
    border: 1px solid black;
    width: 90%;
    height: 500px;
    box-shadow:  2px 2px 4px 2px #888;
    background-color: #c3aeb5;
    text-align: center;
    text-justify: auto;

}
